import Sidebar from "@components/sidebar";
import classnames from "classnames";
import { Editor } from "react-draft-wysiwyg";
import { useEffect, useState } from "react";
import { Controller, useForm } from "react-hook-form";
import { FormattedMessage, injectIntl } from "react-intl";
import "react-phone-number-input/style.css";
import { useDispatch, useSelector } from "react-redux";
import { Button, Form, FormGroup, Input, Label } from "reactstrap";
import avatarBlank from "./../../../../../assets/images/avatars/bg-blank.png";
import validateOptions from "../../../../../constants/validate";
import { convertToRaw, EditorState } from "draft-js";
import draftToHtml from "draftjs-to-html";
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";
import Select from "react-select";
import { convertFileToBase64, uploadImage } from "../../../../../helper/common";
import { addDocument } from "../store/action";
import { scrollToTop } from "../../../../../utility/Utils";

const SidebarNewCourseDocument = ({
  open,
  toggleSidebar,
  intl,
  disable,
  setDisable,
}) => {
  const store = useSelector((state) => state.category);
  const [changeSelect, setChangeSelect] = useState();
  const { product } = useSelector((state) => state.course_document);
  const lang = useSelector((state) => state.common.language);
  const dispatch = useDispatch();
  const document_course = yup.object({
    productId: yup
      .number()
      .required(<FormattedMessage id="The product field is required" />),
    name: yup
      .string()
      .required(<FormattedMessage id="The document name field is required" />)
      .min(
        3,
        <FormattedMessage id="Document name must be at least 3 characters" />
      )
      .max(70, <FormattedMessage id="Document must be 70 characters max" />),
    author: yup
      .string()
      .required(<FormattedMessage id="The Author name field is required" />)
      .min(
        3,
        <FormattedMessage id="Author name must be at least 3 characters" />
      )
      .max(70, <FormattedMessage id="Author must be 70 characters max" />),
    fileurl: yup.string(),
    sort_order: yup
      .number()
      .required(<FormattedMessage id="The sort order field is required" />),
    //  {
    //   required: <FormattedMessage id="The sort order field is required" />,
    //   pattern: {
    //     value: /^\d+$/,
    //     message: <FormattedMessage id="Invalid  number" />,
    //   },
    // },
  });
  const {
    register,
    formState: { errors },
    handleSubmit,
    control,
    setError,
    setValue,
  } = useForm({
    resolver: yupResolver(document_course),
    mode: "all",
  });

  useEffect(() => {
    if (store?.err?.statusCode) {
      setDisable(false);
    }
  }, [store?.err]);
  const [valueContent, setValueContent] = useState(EditorState.createEmpty());
  const [valueShortContent, setValueShortContent] = useState(
    EditorState.createEmpty()
  );
  const [urlFile, setUrlFile] = useState("");
  const [selectedFile, setSelectedFile] = useState();
  const [File, setFile] = useState();
  const handleFileSelect = async (event) => {
    const file = event.target.files[0];
    const imageBase64 = await convertFileToBase64(file);
    setFile(file);
    setSelectedFile(imageBase64);
  };
  useEffect(() => {
    (async () => {
      if (selectedFile) {
        const newData = selectedFile.replace(/,/gi, "").split("base64");
        if (newData[1]) {
          const data = {
            imageData: newData[1],
            imageFormat: "pdf",
          };
          await uploadImage(data, File).then((res) => {
            setUrlFile(res?.data);
            const fileurl = document.getElementById("fileurl");
            fileurl.value = res?.data;
          });
        }
      }
    })();
  }, [selectedFile, File]);

  const onSubmit = async (values) => {
    console.log(values);
    const content =
      (valueContent &&
        draftToHtml(convertToRaw(valueContent?.getCurrentContent()))) ||
      null;
    const short_content =
      (valueShortContent &&
        draftToHtml(convertToRaw(valueShortContent?.getCurrentContent()))) ||
      null;
    try {
      dispatch(
        addDocument({
          ...values,
          status: parseInt(values.status),
          sort_order: values?.sort_order ? parseInt(values?.sort_order) : 1,
          show_download: parseInt(values.show_download),
          content,
          lang,
          short_content,
        })
      );
    } catch (error) {
      console.log(error);
    }
  };

  const uploadCallback = async (file) => {
    var linkImg = avatarBlank;
    const imageBase64 = await convertFileToBase64(file);
    if (imageBase64) {
      const newData = imageBase64.replace(/,/gi, "").split("base64");
      if (newData[1]) {
        const data = {
          imageData: newData[1],
          imageFormat: file?.type?.split("/")[1] || "png",
        };
        await uploadImage(data, file).then((res) => {
          linkImg = res?.data;
        });
      }
    }
    return new Promise((resolve, reject) => {
      resolve({ data: { link: linkImg } });
    });
  };
  return (
    <Sidebar
      size="lg"
      open={open}
      title={<FormattedMessage id="Add New Document" />}
      headerClassName="mb-1"
      contentClassName="pt-0"
      toggleSidebar={toggleSidebar}
    >
      <Form onSubmit={handleSubmit(onSubmit)}>
        <FormGroup>
          <Label for="sort_order">
            <FormattedMessage id="sort_order" />{" "}
          </Label>
          <Input
            name="sort_order"
            id="sort_order"
            placeholder=" "
            defaultValue="1"
            innerRef={register(document_course.sort_order)}
            onBlur={() => {
              let sort_order = document.getElementById("sort_order");
              if (sort_order && sort_order.value) {
                sort_order.value = sort_order.value.trim();
              }
            }}
            className={classnames({ "is-invalid": errors["sort_order"] })}
          />
          <small className="text-danger">
            {errors?.sort_order && errors.sort_order.message}
          </small>
        </FormGroup>
        <FormGroup>
          <Label for="name">
            <FormattedMessage id="name" />{" "}
            <span className="text-danger">*</span>
          </Label>
          <Input
            name="name"
            id="name"
            placeholder=""
            innerRef={register(document_course.name)}
            onBlur={() => {
              let name = document.getElementById("name");
              if (name && name.value) {
                name.value = name.value.trim();
              }
            }}
            className={classnames({ "is-invalid": errors["name"] })}
          />
          <small className="text-danger">
            {errors?.name && errors.name.message}
          </small>
          {errors?.name?.type == "validate" && (
            <small className="text-danger">
              <FormattedMessage id="Invalid name" />
            </small>
          )}
        </FormGroup>
        <FormGroup>
          <Label for="author">
            <FormattedMessage id="author" />{" "}
            <span className="text-danger">*</span>
          </Label>
          <Input
            name="author"
            id="author"
            placeholder=""
            innerRef={register(document_course.author)}
            onBlur={() => {
              let author = document.getElementById("author");
              if (author && author.value) {
                author.value = author.value.trim();
              }
            }}
            className={classnames({ "is-invalid": errors["author"] })}
          />
          <small className="text-danger">
            {errors?.author && errors.author.message}
          </small>
          {errors?.author?.type == "validate" && (
            <small className="text-danger">
              <FormattedMessage id="Invalid author" />
            </small>
          )}
        </FormGroup>
        <FormGroup>
          <Label for="fileurl">
            <FormattedMessage id="fileurl" />{" "}
          </Label>
          <Input
            name="fileurl"
            id="fileurl"
            placeholder=""
            defaultValue={urlFile}
            innerRef={register(document_course.fileurl)}
            onBlur={() => {
              let fileurl = document.getElementById("fileurl");
              if (fileurl && fileurl.value) {
                fileurl.value = fileurl.value.trim();
              }
            }}
            className={classnames({ "is-invalid": errors["fileurl"] })}
          />
          <small className="text-danger">
            {errors?.fileurl && errors.fileurl.message}
          </small>
          {errors?.fileurl?.type == "validate" && (
            <small className="text-danger">
              <FormattedMessage id="Invalid fileurl" />
            </small>
          )}
        </FormGroup>
        <FormGroup>
          <Input
            type="file"
            name="fileurl"
            id="fileurl"
            onChange={handleFileSelect}
            className={classnames({ "is-invalid": errors["fileurl"] })}
          />
          <small className="text-danger">
            {errors?.fileurl && errors.fileurl.message}
          </small>
          {errors?.fileurl?.type == "validate" && (
            <small className="text-danger">
              <FormattedMessage id="Invalid fileurl" />
            </small>
          )}
        </FormGroup>
        <FormGroup>
          <Label for="content">
            <FormattedMessage id="Content" />{" "}
            <span className="text-danger"></span>
          </Label>

          <Editor
            stripPastedStyles={true}
            toolbar={{
              // options: ["inline", "textAlign"],
              // inline: {
              //   inDropdown: false,
              //   options: ["bold", "italic", "underline"],
              // },
              image: { uploadCallback: uploadCallback },
            }}
            editorState={valueContent}
            onEditorStateChange={(data) => setValueContent(data)}
            name="content"
            // innerRef={register(Document_course.content)}
          />
          <small className="text-danger">
            {errors?.content && errors.content.message}
          </small>
        </FormGroup>
        <FormGroup>
          <Label for="short_content">
            <FormattedMessage id="short_content" />{" "}
            <span className="text-danger"></span>
          </Label>

          <Editor
            stripPastedStyles={true}
            toolbar={{
              options: ["inline", "textAlign"],
              inline: {
                inDropdown: false,
                options: ["bold", "italic", "underline"],
              },
            }}
            editorState={valueShortContent}
            onEditorStateChange={(data) => setValueShortContent(data)}
            name="short_content"
            // innerRef={register(Document_course.short_content)}
          />
          <small className="text-danger">
            {errors?.short_content && errors.short_content.message}
          </small>
        </FormGroup>

        <FormGroup>
          <Label>
            <FormattedMessage id="Course" />
            <span className="text-danger"> * </span>
          </Label>
          <Controller
            control={control}
            name="productId"
            render={({ field }) => {
              return (
                <Select
                  // isClearable={false}
                  // onChange={(e) => {
                  //   console.log(e);
                  //   setChangeSelect(e?.value);
                  // }}
                  id="productId"
                  innerRef={register}
                  name="productId"
                  placeholder={<FormattedMessage id="Select Course" />}
                  className={classnames(
                    "react-select",
                    !changeSelect
                      ? {
                          "is-invalid": errors["productId"],
                        }
                      : ""
                  )}
                  options={product?.map((item, index) => {
                    return {
                      value: item?.id,
                      label: item?.product_names[0]?.name,
                      number: index + 1,
                    };
                  })}
                  classNamePrefix="select"
                  {...field}
                  onChange={(e) => {
                    setError("productId", "");
                    setValue("productId", e?.value);
                    // field.onChange(e);
                    setChangeSelect(e?.value);
                  }}
                />
              );
            }}
          ></Controller>
          <small className="text-danger">
            {errors?.productId && errors.productId.message}
          </small>
        </FormGroup>

        <FormGroup>
          <Label for="show_download">
            <FormattedMessage id="show_download" />{" "}
          </Label>

          <Input
            type="select"
            name="show_download"
            id="show_download"
            innerRef={register({ required: true })}
          >
            <option value="1">
              {intl.formatMessage({ id: "Allow download" })}
            </option>
            <option value="0">
              {intl.formatMessage({ id: "Do not allow download" })}
            </option>
          </Input>
        </FormGroup>

        <FormGroup>
          <Label for="status">
            <FormattedMessage id="status" />{" "}
          </Label>

          <Input
            type="select"
            name="status"
            id="status"
            innerRef={register({ required: true })}
          >
            <option value="1">{intl.formatMessage({ id: "Active" })}</option>

            <option value="0">{intl.formatMessage({ id: "Deactive" })}</option>
          </Input>
        </FormGroup>

        <div style={{ textAlign: "end" }}>
          <Button
            type="submit"
            className="mr-1"
            color="primary"
            disabled={disable}
          >
            <FormattedMessage id="Save" />
          </Button>
          <Button
            type="reset"
            color="secondary"
            outline
            onClick={toggleSidebar}
          >
            <FormattedMessage id="Cancel" />
          </Button>
        </div>
      </Form>
    </Sidebar>
  );
};

export default injectIntl(SidebarNewCourseDocument);
